html {
  box-sizing: border-box;
  color: #4a5568;
  accent-color: #9f7aea;
  scroll-behavior: smooth;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
  font-size: 15px;
  line-height: 1.5;
}

@media (width >= 426px) {
  html {
    font-size: 16px;
  }
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

main {
  margin: 2em 1em;
}

a {
  color: #63b3ed;
  text-decoration: none;
}

a:focus {
  outline: none;
}

a:hover, a:focus {
  text-decoration: underline;
}

h1, h2, h3, h4, h5, h6 {
  color: #1a202c;
}

.logo {
  border: 2px dashed #9f7aea;
  margin-bottom: 0;
  font-family: Lexend Deca, Helvetica, sans-serif;
  font-size: 2rem;
  line-height: 1.5;
  display: inline-flex;
}

.logo__square {
  border-right: 2px dashed #63b3ed;
  width: 1.5em;
  height: 1.5em;
}

.logo__title {
  white-space: nowrap;
  padding: 0 .5em;
}

@media (width >= 769px) {
  .logo {
    font-size: 4rem;
  }
}

code, pre {
  background-color: #f7fafc;
  border-radius: .3em;
}

code {
  text-transform: none;
  padding: .3em .5em;
  font-family: sfmono-regular, consolas, Liberation Mono, menlo, monospace;
  font-size: .85rem;
  font-weight: 300;
}

pre {
  tab-size: 2;
  padding: 1.5em;
  line-height: 1.2;
  overflow-x: auto;
}

pre code {
  background-color: #0000;
  padding: 0;
}

pre u {
  text-decoration: none;
}

.code-block {
  color: #93ddfd;
  background-color: #2d3748;
}

.color-primary {
  color: #9f7aea;
}

.color-secondary {
  color: #63b3ed;
}

.color-tertiary {
  color: #4fd1c5;
}

.color-r {
  color: #ff8383;
}

.color-g {
  color: #b5f4a5;
}

.color-y {
  color: #ffe484;
}

.highlight {
  border-radius: .15em;
  padding: 0 .15em;
}

.highlight:hover {
  text-decoration: none;
}

.highlight-primary {
  color: #fff;
  background-color: #9f7aea;
}

.highlight-secondary {
  color: #fff;
  background-color: #63b3ed;
}

.highlight-tertiary {
  color: #fff;
  background-color: #4fd1c5;
}

hr {
  border: 0;
  border-bottom: 2px solid #edf2f7;
  width: 100%;
  height: 0;
  margin: 3em 0;
}

.box {
  text-align: center;
  background-color: #edf2f7;
  width: 100%;
  height: 100%;
  padding: .5em;
  display: inline-block;
}

.toggle-grid-debug {
  z-index: 1;
  color: #9f7aea;
  white-space: nowrap;
  cursor: pointer;
  background: #fff;
  border: 2px dashed;
  padding: 1em;
  animation: .4s ease-out both slide-in;
  display: inline-block;
  position: fixed;
  bottom: 1em;
  right: 1em;
}

@keyframes slide-in {
  from {
    opacity: 0;
    transform: translateY(100%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.toggle-grid-debug:hover, .toggle-grid-debug:focus {
  opacity: .8;
}

.toggle-grid-debug-checkbox {
  vertical-align: middle;
  margin: 0 .2em 0 0;
}

.anchor {
  position: relative;
}

.anchor-link {
  opacity: 0;
  padding-right: .25rem;
  font-style: normal;
  font-weight: 300;
  position: absolute;
  right: 100%;
}

.anchor-link:focus, .anchor:hover .anchor-link {
  opacity: 1;
  text-decoration: none;
}

.counter {
  counter-reset: index-1;
}

.counter .counter-index-1 {
  counter-reset: index-2;
  counter-increment: index-1;
}

.counter .counter-index-1:before {
  content: counter(index-1) "." " ";
}

.counter .counter-index-2 {
  counter-reset: index-3;
  counter-increment: index-2;
}

.counter .counter-index-2:before {
  content: counter(index-1) "."counter(index-2) "." " ";
}

.counter .counter-index-3 {
  counter-reset: index-4;
  counter-increment: index-3;
}

.counter .counter-index-3:before {
  content: counter(index-1) "."counter(index-2) "."counter(index-3) "." " ";
}

.github-corner {
  z-index: 1;
  color: #fff;
  fill: #63b3ed;
  position: fixed;
  top: 0;
  right: 0;
}

.github-corner:hover .octo-arm {
  animation: .56s ease-in-out octocat-wave;
}

@keyframes octocat-wave {
  0%, 100% {
    transform: rotate(0);
  }

  20%, 60% {
    transform: rotate(-25deg);
  }

  40%, 80% {
    transform: rotate(10deg);
  }
}

@media (width <= 500px) {
  .github-corner:hover .octo-arm {
    animation: none;
  }

  .github-corner .octo-arm {
    animation: .56s ease-in-out octocat-wave;
  }
}
